// --- Helper Functions ---
const formatTime = (seconds: number): string => {
  const minutes = Math.floor(seconds / 60);
  const secs = Math.floor(seconds % 60);
  return `${String(minutes).padStart(2, "0")}:${String(secs).padStart(2, "0")}`;
};

// --- Video Player Component ---
interface VideoPlayerProps {
  currentTime: number;
  duration: number;
  isPlaying: boolean;
  onPlayPause: () => void;
  onSeek: (time: number) => void;
}

const VideoPlayer: React.FC<VideoPlayerProps> = ({
  currentTime,
  duration,
  isPlaying,
  onPlayPause,
  onSeek,
}) => {
  const progress = duration > 0 ? (currentTime / duration) * 100 : 0;

  return (
    <GlassCard className="p-3 m-2">
      <div className="aspect-video bg-black/70 rounded-lg flex items-center justify-center mb-2">
        <p className="text-2xl font-bold text-[#FFA896]">Mock Video Screen</p>
      </div>
      <div className="flex items-center justify-between text-[#F9F9F9]">
        <button
          onClick={onPlayPause}
          className="p-2 rounded-full hover:bg-[#CD1C18]/50 transition-colors"
        >
          {isPlaying ? (
            <PauseIcon className="w-6 h-6" />
          ) : (
            <PlayIcon className="w-6 h-6" />
          )}
        </button>
        <div className="text-sm tabular-nums">
          {formatTime(currentTime)} / {formatTime(duration)}
        </div>
      </div>
      <input
        type="range"
        min="0"
        max={duration}
        value={currentTime}
        onChange={(e) => onSeek(Number(e.target.value))}
        className="w-full h-2 rounded-lg appearance-none cursor-pointer accent-[#CD1C18] bg-[#F9F9F9]/30 mt-1"
      />
    </GlassCard>
  );
};

// --- Annotation Sidebar Component ---
interface AnnotationSidebarProps {
  isOpen: boolean;
  onClose: () => void;
  annotations: Annotation[];
  currentVideoTime: number;
  onAddAnnotation: (text: string) => void;
}

const AnnotationSidebar: React.FC<AnnotationSidebarProps> = ({
  isOpen,
  onClose,
  annotations,
  currentVideoTime,
  onAddAnnotation,
}) => {
  const [newAnnotationText, setNewAnnotationText] = useState("");
  const touchStartX = useRef(0);
  const touchEndX = useRef(0);

  const handleTouchStart = (e: React.TouchEvent) => {
    touchStartX.current = e.targetTouches[0].clientX;
    touchEndX.current = 0; // Reset touchEndX
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    touchEndX.current = e.targetTouches[0].clientX;
  };

  const handleTouchEnd = () => {
    if (touchStartX.current === 0 || touchEndX.current === 0) return; // No swipe detected
    const deltaX = touchEndX.current - touchStartX.current;
    // Swipe right to close (if sidebar is on the right)
    if (deltaX > 50) {
      onClose();
    }
    // Reset refs
    touchStartX.current = 0;
    touchEndX.current = 0;
  };

  const handleSubmitAnnotation = () => {
    if (newAnnotationText.trim()) {
      onAddAnnotation(newAnnotationText.trim());
      setNewAnnotationText("");
    }
  };

  return (
    <div
      className={`fixed top-0 right-0 h-full w-4/5 max-w-md transform transition-transform duration-300 ease-in-out z-40 ${
        isOpen ? "translate-x-0" : "translate-x-full"
      }`}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      <GlassCard className="h-full flex flex-col p-4">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-[#FFA896]">Annotations</h2>
          <button
            onClick={onClose}
            className="p-1 text-[#F9F9F9] hover:text-[#FFA896]"
          >
            <XIcon className="w-6 h-6" />
          </button>
        </div>
        <div className="flex-grow overflow-y-auto space-y-3 pr-1 scrollbar-thin scrollbar-thumb-[#CD1C18] scrollbar-track-transparent">
          {annotations.map((ann) => (
            <div
              key={ann.id}
              className={`p-2 rounded-md bg-[#F9F9F9]/5 border ${
                ann.timestamp <= currentVideoTime
                  ? "border-[#FFA896]"
                  : "border-transparent"
              }`}
            >
              <div className="flex justify-between text-xs text-[#F9F9F9]/70 mb-1">
                <span>@{ann.user}</span>
                <span>{formatTime(ann.timestamp)}</span>
              </div>
              <p className="text-sm text-[#F9F9F9]">{ann.text}</p>
            </div>
          ))}
        </div>
        <div className="mt-4">
          <textarea
            value={newAnnotationText}
            onChange={(e) => setNewAnnotationText(e.target.value)}
            placeholder={`Add annotation @ ${formatTime(currentVideoTime)}...`}
            className="w-full p-2 rounded-md bg-[#3B000A]/50 text-[#F9F9F9] border border-[#F9F9F9]/30 focus:ring-[#CD1C18] focus:border-[#CD1C18] placeholder-[#F9F9F9]/50 text-sm"
            rows={3}
          />
          <button
            onClick={handleSubmitAnnotation}
            className="mt-2 w-full bg-[#CD1C18] text-[#F9F9F9] py-2 px-4 rounded-md hover:bg-[#9B1313] transition-colors font-semibold"
          >
            Add Annotation
          </button>
        </div>
      </GlassCard>
    </div>
  );
};

