// --- Draggable Attendance List Component ---
interface DraggableAttendanceListProps {
  attendees: Attendee[];
}

const DraggableAttendanceList: React.FC<DraggableAttendanceListProps> = ({
  attendees,
}) => {
  const [position, setPosition] = useState({ x: 10, y: 150 });
  const [isDragging, setIsDragging] = useState(false);
  const dragStartPos = useRef({ x: 0, y: 0 });
  const panelRef = useRef<HTMLDivElement>(null);

  const handleMouseDown = (e: React.MouseEvent) => {
    if (panelRef.current) {
      setIsDragging(true);
      dragStartPos.current = {
        x: e.clientX - position.x,
        y: e.clientY - position.y,
      };
      // Prevent text selection while dragging
      e.preventDefault();
    }
  };

  const handleMouseMove = useCallback(
    (e: MouseEvent) => {
      if (isDragging) {
        let newX = e.clientX - dragStartPos.current.x;
        let newY = e.clientY - dragStartPos.current.y;

        // Boundary checks (optional, basic example)
        newX = Math.max(
          0,
          Math.min(
            newX,
            window.innerWidth - (panelRef.current?.offsetWidth || 200)
          )
        );
        newY = Math.max(
          0,
          Math.min(
            newY,
            window.innerHeight - (panelRef.current?.offsetHeight || 150)
          )
        );

        setPosition({ x: newX, y: newY });
      }
    },
    [isDragging]
  );

  const handleMouseUp = useCallback(() => {
    setIsDragging(false);
  }, []);

  useEffect(() => {
    if (isDragging) {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
    } else {
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
    }
    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
    };
  }, [isDragging, handleMouseMove, handleMouseUp]);

  return (
    <div
      ref={panelRef}
      className="fixed z-30 w-48"
      style={{ transform: `translate(${position.x}px, ${position.y}px)` }}
    >
      <GlassCard className="p-0 overflow-hidden">
        <div
          className="bg-[#CD1C18]/80 text-[#F9F9F9] p-2 cursor-move flex justify-between items-center"
          onMouseDown={handleMouseDown}
        >
          <h3 className="text-sm font-semibold">
            Attendees ({attendees.length})
          </h3>
          <UsersIcon className="w-4 h-4" />
        </div>
        <ul className="max-h-40 overflow-y-auto p-2 space-y-1 scrollbar-thin scrollbar-thumb-[#FFA896]/50 scrollbar-track-transparent">
          {attendees.map((att) => (
            <li
              key={att.id}
              className="flex items-center space-x-2 text-xs text-[#F9F9F9]"
            >
              <img
                src={att.avatar}
                alt={att.name}
                className="w-5 h-5 rounded-full"
              />
              <span>{att.name}</span>
            </li>
          ))}
        </ul>
      </GlassCard>
    </div>
  );
};

// --- Floating Progress Ring Component ---
interface FloatingProgressRingProps {
  progress: number; // 0 to 100
}

const FloatingProgressRing: React.FC<FloatingProgressRingProps> = ({
  progress,
}) => {
  const radius = 30;
  const circumference = 2 * Math.PI * radius;
  const offset = circumference - (progress / 100) * circumference;

  return (
    <div className="fixed bottom-4 right-4 z-30 w-16 h-16">
      <GlassCard className="w-full h-full rounded-full flex items-center justify-center p-0.5">
        <svg className="w-full h-full transform -rotate-90">
          <circle
            className="text-[#F9F9F9]/20"
            strokeWidth="5"
            stroke="currentColor"
            fill="transparent"
            r={radius}
            cx="50%"
            cy="50%"
          />
          <circle
            className="text-[#FFA896]"
            strokeWidth="5"
            strokeDasharray={circumference}
            strokeDashoffset={offset}
            strokeLinecap="round"
            stroke="currentColor"
            fill="transparent"
            r={radius}
            cx="50%"
            cy="50%"
          />
        </svg>
        <span className="absolute text-xs font-semibold text-[#F9F9F9]">
          {Math.round(progress)}%
        </span>
      </GlassCard>
    </div>
  );
};

// --- Comment Thread Component (Virtualized) ---
interface CommentThreadProps {
  comments: Comment[];
  onAddComment: (text: string) => void;
}

const CommentThread: React.FC<CommentThreadProps> = ({
  comments,
  onAddComment,
}) => {
  const [newComment, setNewComment] = useState("");
  const listRef = useRef<FixedSizeList>(null);
  const containerRef = useRef<HTMLDivElement>(null); // Ref for the container to get its height

  const handleAddComment = () => {
    if (newComment.trim()) {
      onAddComment(newComment.trim());
      setNewComment("");
      // Scroll to bottom after adding new comment
      if (listRef.current) {
        listRef.current.scrollToItem(comments.length, "end");
      }
    }
  };

  // Render each comment row
  const Row = ({ index, style }: ListChildComponentProps) => {
    const comment = comments[index];
    return (
      <div style={style} className="px-2 py-1">
        <GlassCard className="p-2 flex space-x-2">
          <img
            src={comment.avatar}
            alt={comment.user}
            className="w-8 h-8 rounded-full flex-shrink-0 mt-1"
          />
          <div className="flex-grow">
            <p className="text-xs font-semibold text-[#FFA896]">
              {comment.user}
            </p>
            <p className="text-sm text-[#F9F9F9] break-words">{comment.text}</p>
          </div>
        </GlassCard>
      </div>
    );
  };

  // Determine list height dynamically
  const [listHeight, setListHeight] = useState(300); // Default height
  useEffect(() => {
    if (containerRef.current) {
      setListHeight(containerRef.current.clientHeight);
    }
    const handleResize = () => {
      if (containerRef.current) {
        setListHeight(containerRef.current.clientHeight);
      }
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <div className="flex flex-col flex-grow m-2 min-h-0">
      {" "}
      {/* min-h-0 is crucial for flex item with overflow */}
      <h3 className="text-lg font-semibold text-[#FFA896] px-2 mb-2">
        Discussion
      </h3>
      <div ref={containerRef} className="flex-grow relative">
        {" "}
        {/* This div will be measured for height */}
        {listHeight > 0 && (
          <FixedSizeList
            ref={listRef}
            height={listHeight}
            itemCount={comments.length}
            itemSize={100} // Approximate height of a comment item, adjust as needed
            width="100%"
            className="scrollbar-thin scrollbar-thumb-[#CD1C18] scrollbar-track-transparent"
          >
            {Row}
          </FixedSizeList>
        )}
      </div>
      <GlassCard className="mt-2 p-2">
        <div className="flex space-x-2">
          <input
            type="text"
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
            placeholder="Type your comment..."
            className="flex-grow p-2 rounded-md bg-[#3B000A]/50 text-[#F9F9F9] border border-[#F9F9F9]/30 focus:ring-[#CD1C18] focus:border-[#CD1C18] placeholder-[#F9F9F9]/50 text-sm"
            onKeyPress={(e) => e.key === "Enter" && handleAddComment()}
          />
          <button
            onClick={handleAddComment}
            className="bg-[#CD1C18] text-[#F9F9F9] py-2 px-4 rounded-md hover:bg-[#9B1313] transition-colors font-semibold text-sm"
          >
            Send
          </button>
        </div>
      </GlassCard>
    </div>
  );
};

// --- Main Study Group App Component ---
export const StudyGroupApp: React.FC = () => {
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(900); // 15 minutes
  const [isPlaying, setIsPlaying] = useState(false);
  const [studyProgress, setStudyProgress] = useState(30); // Example progress

  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [annotations, setAnnotations] = useState<Annotation[]>(mockAnnotations);
  const [comments, setComments] = useState<Comment[]>(mockComments);

  // Video playback simulation
  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isPlaying && currentTime < duration) {
      timer = setInterval(() => {
        setCurrentTime((prevTime) => Math.min(prevTime + 1, duration));
      }, 1000);
    }
    if (currentTime >= duration) {
      setIsPlaying(false);
    }
    return () => clearInterval(timer);
  }, [isPlaying, currentTime, duration]);

  // Update study progress based on video time (example logic)
  useEffect(() => {
    setStudyProgress(duration > 0 ? (currentTime / duration) * 100 : 0);
  }, [currentTime, duration]);

  const handlePlayPause = () => {
    if (currentTime >= duration && !isPlaying) {
      // If video ended, restart
      setCurrentTime(0);
    }
    setIsPlaying(!isPlaying);
  };

  const handleSeek = (time: number) => {
    setCurrentTime(time);
  };

  const handleAddAnnotation = (text: string) => {
    const newAnnotation: Annotation = {
      id: `ann-${Date.now()}`,
      timestamp: Math.floor(currentTime),
      text,
      user: "You", // Current user (mocked)
    };
    setAnnotations((prev) =>
      [...prev, newAnnotation].sort((a, b) => a.timestamp - b.timestamp)
    );
  };

  const handleAddComment = (text: string) => {
    const newComment: Comment = {
      id: `comment-${Date.now()}`,
      user: "You",
      text,
      avatar: "https://i.pravatar.cc/40?u=currentUser",
    };
    setComments((prev) => [...prev, newComment]);
  };

  const toggleAnnotationSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen);
  };

  const handleBodyTouchStart = (e: React.TouchEvent) => {
    // If sidebar is open and touch is outside sidebar, potential gesture to close.
    // This example focuses on swipe-to-close FROM the sidebar itself.
    // More complex gesture handling (e.g. swipe from edge of screen) would need careful implementation.
  };

  return (
    <div
      className="h-screen w-screen flex flex-col overflow-hidden bg-gradient-to-br from-[#3B000A] via-[#5f101c] to-[#9B1313]"
      onTouchStart={handleBodyTouchStart} // For global gesture listeners if needed
    >
      {/* Header for sidebar toggle */}
      <header className="p-2 flex justify-end items-center text-[#F9F9F9] bg-[#3B000A]/30">
        <button
          onClick={toggleAnnotationSidebar}
          className="p-2 rounded-md hover:bg-[#CD1C18]/50"
        >
          <MessageIcon className="w-6 h-6" />
          <span className="sr-only">Toggle Annotations</span>
        </button>
      </header>

      {/* Main content area */}
      <main className="flex-grow flex flex-col overflow-hidden">
        {" "}
        {/* Ensure main content area can scroll if needed, and flex-grow works */}
        <VideoPlayer
          currentTime={currentTime}
          duration={duration}
          isPlaying={isPlaying}
          onPlayPause={handlePlayPause}
          onSeek={handleSeek}
        />
        <EmojiPulseSurvey />
        {/* CommentThread will take remaining space */}
        <CommentThread comments={comments} onAddComment={handleAddComment} />
      </main>

      <AnnotationSidebar
        isOpen={isSidebarOpen}
        onClose={() => setIsSidebarOpen(false)}
        annotations={annotations}
        currentVideoTime={currentTime}
        onAddAnnotation={handleAddAnnotation}
      />

      <DraggableAttendanceList attendees={mockAttendees} />
      <FloatingProgressRing progress={studyProgress} />

      {/* Overlay for when sidebar is open, to close on tap outside */}
      {isSidebarOpen && (
        <div
          className="fixed inset-0 bg-black/30 z-30 md:hidden" // Only show on mobile for tap-outside-to-close
          onClick={() => setIsSidebarOpen(false)}
        ></div>
      )}
    </div>
  );
};

export default StudyGroupApp;

